'use client';

import { useState } from 'react';
import Link from 'next/link';
import BottomNavigation from '../components/BottomNavigation';

export default function ProfilePage() {
    const [user] = useState({
        name: 'John A.',
        trustScore: 4.8,
        avatar: '/api/placeholder/100/100',
        hasIdPhoto: false
    });

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2Z" />
                    </svg>
                </div>
            </div>

            {/* Header */}
            <div className="text-center px-8 mb-8">
                <h1 className="text-3xl font-bold">Profile</h1>
            </div>

            {/* Profile Content */}
            <div className="flex-1 px-8">
                {/* User Avatar and Name */}
                <div className="text-center mb-8">
                    <div className="w-24 h-24 bg-gray-600 rounded-full flex items-center justify-center mx-auto mb-4">
                        <svg className="w-16 h-16 text-gray-300" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2ZM21 9V7L15 1H5C3.9 1 3 1.9 3 3V21C3 22.1 3.9 23 5 23H19C20.1 23 21 22.1 21 21V9M19 21H5V3H13V9H19V21Z" />
                        </svg>
                    </div>

                    <h2 className="text-2xl font-bold text-white mb-2">{user.name}</h2>
                    <button className="text-gray-400 text-lg">Edit Profile</button>
                </div>

                {/* ID Photo Section */}
                <div className="bg-gray-900 rounded-lg p-4 mb-6">
                    <div className="flex items-center justify-between">
                        <div className="flex items-center space-x-3">
                            <div className="w-8 h-8 bg-gray-700 rounded-lg flex items-center justify-center">
                                <span className="text-xl font-bold">+</span>
                            </div>
                            <div>
                                <h3 className="text-white font-medium">ID Photo</h3>
                                <p className="text-gray-400 text-sm">Add an ID for verification</p>
                            </div>
                        </div>
                        <svg className="w-5 h-5 text-gray-400" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M9 6l6 6-6 6" />
                        </svg>
                    </div>
                </div>

                {/* Trust Score Section */}
                <div className="bg-gray-900 rounded-lg p-6 mb-6">
                    <div className="flex items-center justify-between">
                        <div>
                            <h3 className="text-white text-lg font-bold mb-2">Pickom</h3>
                            <p className="text-gray-400">People-Powered Delivery</p>
                        </div>
                        <div className="text-right">
                            <div className="flex items-center space-x-1 mb-1">
                                {[1, 2, 3, 4, 5].map((star) => (
                                    <svg
                                        key={star}
                                        className={`w-5 h-5 ${star <= Math.floor(user.trustScore) ? 'text-yellow-400' : 'text-gray-600'
                                            }`}
                                        viewBox="0 0 24 24"
                                        fill="currentColor"
                                    >
                                        <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
                                    </svg>
                                ))}
                                <svg className="w-5 h-5 text-gray-600" viewBox="0 0 24 24" fill="none" stroke="currentColor">
                                    <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
                                </svg>
                            </div>
                            <p className="text-white font-medium">{user.trustScore} Trust Score</p>
                        </div>
                    </div>
                </div>

                {/* Quick Actions */}
                <div className="space-y-4">
                    <Link
                        href="/wallet"
                        className="block bg-gray-900 rounded-lg p-4 hover:bg-gray-800 transition-colors"
                    >
                        <div className="flex items-center justify-between">
                            <span className="text-white text-lg">Wallet</span>
                            <svg className="w-5 h-5 text-gray-400" viewBox="0 0 24 24" fill="currentColor">
                                <path d="M9 6l6 6-6 6" />
                            </svg>
                        </div>
                    </Link>

                    <Link
                        href="/orders"
                        className="block bg-gray-900 rounded-lg p-4 hover:bg-gray-800 transition-colors"
                    >
                        <div className="flex items-center justify-between">
                            <span className="text-white text-lg">Order History</span>
                            <svg className="w-5 h-5 text-gray-400" viewBox="0 0 24 24" fill="currentColor">
                                <path d="M9 6l6 6-6 6" />
                            </svg>
                        </div>
                    </Link>

                    <Link
                        href="/notifications"
                        className="block bg-gray-900 rounded-lg p-4 hover:bg-gray-800 transition-colors"
                    >
                        <div className="flex items-center justify-between">
                            <span className="text-white text-lg">Notifications</span>
                            <svg className="w-5 h-5 text-gray-400" viewBox="0 0 24 24" fill="currentColor">
                                <path d="M9 6l6 6-6 6" />
                            </svg>
                        </div>
                    </Link>

                    <Link
                        href="/help"
                        className="block bg-gray-900 rounded-lg p-4 hover:bg-gray-800 transition-colors"
                    >
                        <div className="flex items-center justify-between">
                            <span className="text-white text-lg">Help Center</span>
                            <svg className="w-5 h-5 text-gray-400" viewBox="0 0 24 24" fill="currentColor">
                                <path d="M9 6l6 6-6 6" />
                            </svg>
                        </div>
                    </Link>
                </div>
            </div>

            {/* Bottom Navigation */}
            <BottomNavigation />
        </div>
    );
} 